body {
  margin: 0;
  font-family: "Roboto", Arial !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #d5d4d4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

async-paginate-container{
    width: 100%;
    position: center;
    display: flex;
}
.search-container {
    width: 30%;
    margin: 20px auto;
    /* change the margin to move where it is on the page */
}
.container{
  max-width: 1080px;
  margin: 20px auto;
}
.weather{
    width: 200px;
    border-radius: 20px;
    box-shadow: 10px -2px 20px 2px rgb(0 0 0 / 30%);
    color: #fff;
    background-color: #333;
    margin: 20px auto 0 auto;
    padding: 0 20px 20px 20px;
    height: 150px;
}
.top, .bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.city{
    font-weight: 600;
    font-size: 15px;
    line-height: 1;
    margin: 0;
    letter-spacing: 1px;
}
.weather-description{
    font-weight: 400;
    font-size: 14px;
    line-height: 1;
    margin: 0;
}
.weather-icon{
    width: 50px;
}
.details{
    width: 100%;
    padding-left: 20px;
}
.temperature{
    font-weight: 450;
    font-size: 35px;
    width: auto;
    letter-spacing: 2px;
    margin: 10px 0;
    color: white;
}
.parameter-row{
    display: flex;
    justify-content: space-between;
}
.parameter-label{
    text-align: left;
    font-weight: 400;
    font-size: 12px;
    margin-right: 4px;
}
.parameter-value{
    text-align: right;
    font-weight: 600;
    font-size: 12px;
}
.title {
  font-size: 23px;
  font-weight: 700;
  position: absolute;
  /*top: 50%;*/
  left: 50%;
  top: 37%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: inline-block; /* add this line */
}
  
  .daily-item {
    background-color: #f5f5f5;
    border-radius: 15px;
    height: 40px;
    margin: 5px;
    align-items: center;
    cursor: pointer;
    display: flex;
    font-size: 14px;
    padding: 5px 20px;
    Width: 40%;
    position: relative;
    top: 50%;
    margin: 60px 5px 5px 5px;
  }
  
  .icon-small {
    width: 40px;
  }
  
  .daily-item .day {
    cursor: inherit;
    color: #212121;
    flex: 1 1;
    font-weight: 300;
    margin-left: 5px;
  }
  
  .description {
    cursor: inherit;
    flex: 1 1;
    margin-right: 40px;
    text-align: right;
    font-size: 15px;
  }
  
  .min-max {
    color: #757575;
  }
  
  .daily-details-grid {
    grid-row-gap: 0;
    grid-column-gap: 15px;
    -webkit-column-gap: 15px;
    grid-column-gap: 15px;
    column-gap: 15px;
    display: grid;
    flex: 1 1;
    grid-template-columns: auto auto;
    padding: 5px 15px;
    grid-row-gap: 0;
    row-gap: 0;
    justify-content: start;
    align-items: stretch;
    justify-items: stretch;
  }
  
  .daily-details-grid-item {
    align-items: center;
    display: flex;
    height: 30px;
    /*justify-content: space-between;*/
  }
  
  .daily-details-grid-item label:first-child {
    color: #757575;
  }
  
  .daily-details-grid-item label:last-child {
    color: #212121;
  }
